.table {
  max-width     : 1000px;
  display       : flex;
  flex-direction: row;
}

.playerscolumn {
  list-style-type     : none;
  min-width: 10%;
  max-width               : 15%;
  display             : flex;
  flex-direction      : column;
  padding-inline-start: 0px;
}

.dealscontainer {
  width         : 60%;
  display       : flex;
  flex-direction: row;
}

.dealcolumn {
  width          : 14.28%;
  display        : flex;
  list-style-type: none;
  flex-direction : column;

  .dealheader {
    display: flex;
  }

  input {
    height: 15px;
    text-align: right;
  }
}

.truncated {
  display      : inline;
  white-space  : nowrap;
  overflow     : hidden;
  text-overflow: ellipsis;
}

.nameheader {
  .truncated();
}

.playername {
  .truncated();
}

.dealheader {
  .truncated();
}

.resultcolumn {
  width         : 7.5%;
  display       : flex;
  flex-direction: column;

  .dealheader {
    display: flex;
  }
}